.container {
  width: 100%;
  padding: 0 10px;

  @media (min-width: $viewpoint--desktop) {
    max-width: 1200px;
    padding: 0 20px;
    margin-left: auto;
    margin-right: auto;
  }
}

.container--pads {
  padding-top: 50px;
  padding-bottom: 40px;

  @media (min-width: $viewpoint--desktop) {
    padding-top: 80px;
    padding-bottom: 60px;
  }
}

.no-mobile {
  display: none;

  @media (min-width: $viewpoint--desktop) {
    display: block;
  }
}
