.banner {
  display: none;

  background-color: $bg-main-red;

  margin-right: auto;
  margin-left: auto;

  padding-top: 60px;
  padding-bottom: 60px;

  @media (min-width: $viewpoint--desktop) {
    display: block;
  }
}

.banner__text {
  font-size: 96px;
  margin: 0;
  padding: 0;
  line-height: 115px;
  font-family: $font-family--bebas;
}
