*,
*::before,
*::after {
  box-sizing: border-box;
}

html {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  width: 100%;
  height: 100%;
}

img {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email], [type=number], [type=password], [type=search], [type=tel], [type=text], [type=url]) {
  appearance: none;
}

@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Bebas Neue";
  font-display: swap;
  src: url("../../fonts/BebasNeue-Regular.woff2") format("woff2"), url("../../fonts/BebasNeue-Regular.woff") format("woff");
}
@font-face {
  font-style: normal;
  font-weight: 400;
  font-family: "Roboto";
  font-display: swap;
  src: url("../../fonts/Roboto-Regular.woff2") format("woff2"), url("../../fonts/Roboto-Regular.woff") format("woff");
}
html,
body {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 24px;
  font-family: "Roboto", sans-serif;
  color: #efefef;
}
@media (min-width: 700px) {
  html,
body {
    font-size: 24px;
    line-height: 32px;
  }
}

a {
  color: inherit;
  text-decoration: none;
}

.section_header {
  font-family: "Bebas Neue", sans-serif;
  font-size: 64px;
  line-height: 77px;
  padding: 0;
  margin: 0;
}

.section_header--pads {
  padding-top: 50px;
  padding-bottom: 50px;
}

.section_subheader {
  font-family: "Bebas Neue", sans-serif;
  font-size: 36px;
  line-height: 43px;
  padding: 0;
}

.text__secondary {
  color: #A4A9AF;
  text-align: justify;
}

.container {
  width: 100%;
  padding: 0 10px;
}
@media (min-width: 700px) {
  .container {
    max-width: 1200px;
    padding: 0 20px;
    margin-left: auto;
    margin-right: auto;
  }
}

.container--pads {
  padding-top: 50px;
  padding-bottom: 40px;
}
@media (min-width: 700px) {
  .container--pads {
    padding-top: 80px;
    padding-bottom: 60px;
  }
}

.no-mobile {
  display: none;
}
@media (min-width: 700px) {
  .no-mobile {
    display: block;
  }
}

.page-body {
  margin: 0;
  padding: 0;
  color: #efefef;
  background-color: #14171a;
}

.page-header {
  position: fixed;
  width: 100%;
  z-index: 100;
  background-color: #0B0D0F;
}

.page-header__wrapper {
  width: 100%;
  min-height: 60px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
@media (min-width: 700px) {
  .page-header__wrapper {
    justify-content: space-between;
    align-items: center;
  }
}

.page-header__logo {
  display: none;
  width: 44px;
  height: 51px;
}
@media (min-width: 700px) {
  .page-header__logo {
    display: block;
  }
}

.page-header__burger-btn {
  padding: 0;
  height: 60px;
  color: #efefef;
  background-color: transparent;
  border: none;
  border-radius: 0;
  cursor: pointer;
}
.page-header__burger-btn svg {
  fill: currentColor;
}
.page-header__burger-btn:hover {
  color: #A4A9AF;
}
.page-header__burger-btn:active {
  opacity: 0.3;
}
@media (min-width: 700px) {
  .page-header__burger-btn {
    display: none;
  }
}
.page-header__burger-btn .page-header__menu-burger {
  display: block;
}
.page-header__burger-btn .page-header__menu-cross {
  display: none;
}
.page-header__burger-btn--active .page-header__menu-burger {
  display: none;
}
.page-header__burger-btn--active .page-header__menu-cross {
  display: block;
  margin-bottom: 10px;
  margin-top: 10px;
}

.nav-menu {
  display: none;
  width: 100%;
}
@media (min-width: 700px) {
  .nav-menu {
    width: auto;
    display: flex;
    align-items: center;
  }
}
.nav-menu--open {
  display: block;
}
.nav-menu--no-js {
  display: block;
}

.nav-menu__list {
  display: flex;
  flex-wrap: wrap;
  margin: 0;
  padding: 0;
}

.nav-menu__item {
  width: 100%;
  margin-top: 1px;
  list-style: none;
  font-weight: bold;
  font-size: 18px;
  line-height: 32px;
  text-align: center;
  color: #ffffff;
  box-shadow: 0 1px 0 #000000, inset 0 1px 0 rgba(255, 255, 255, 0.15);
}
@media (min-width: 700px) {
  .nav-menu__item {
    width: auto;
    padding-right: 20px;
    padding-left: 20px;
    font-weight: normal;
    line-height: 18px;
    background-color: transparent;
    box-shadow: none;
  }
  .nav-menu__item:last-child {
    padding-right: 0;
  }
}

.nav-menu__link {
  display: block;
  padding-top: 16px;
  padding-bottom: 15px;
  font-family: "Bebas Neue", sans-serif;
  font-size: 24px;
  line-height: 28px;
}
@media (min-width: 700px) {
  .nav-menu__link {
    padding-top: 10px;
    padding-bottom: 10px;
    border-bottom: 2px solid transparent;
  }
  .nav-menu__link:hover {
    color: #A4A9AF;
  }
}
.nav-menu__link:active {
  color: #ffffff;
  opacity: 0.3;
}

.main-banner {
  background-color: #0B0D0F;
  overflow: hidden;
  min-height: 1200px;
}

.main-banner__container {
  position: relative;
}

.main-banner__logo {
  position: absolute;
  top: 0;
  left: 67%;
  transform: translateX(-50%);
}

.main-banner__header {
  position: absolute;
  padding: 0;
  margin: 0;
  white-space: pre-wrap;
  font-family: "Bebas Neue", sans-serif;
  font-size: 64px;
  line-height: 78px;
}
@media (min-width: 700px) {
  .main-banner__header {
    top: 200px;
    font-size: 121px;
    line-height: 145px;
  }
}

.main-banner__text-content {
  width: 100%;
  background-color: #1D2124;
  margin: 0 auto;
  padding: 35px 20px;
  text-align: justify;
}
@media (min-width: 700px) {
  .main-banner__text-content {
    position: absolute;
    top: 870px;
    left: 50%;
    transform: translateX(-50%);
    max-width: 920px;
    opacity: 0.85;
    font-size: 24px;
    line-height: 28px;
  }
}

.main-banner__text-content p {
  margin: 0;
}

.about__header {
  margin-bottom: 50px;
  text-align: center;
}

.about__subheader {
  margin: 10px 0;
  color: #E76767;
}

.about__grid {
  padding-bottom: 10px;
}
@media (min-width: 700px) {
  .about__grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 40px;
    width: 100%;
    justify-content: space-between;
  }
}

.about__card {
  max-width: 550px;
  margin: 0 auto;
}
.about__text_block {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
}

.training {
  background-color: #0B0D0F;
}

.training__header {
  margin-bottom: 50px;
  text-align: center;
}

.training__subheader {
  margin: 35px auto 18px;
  max-width: 960px;
}

.training__grid {
  display: grid;
  grid-gap: 20px;
  grid-template-columns: 1fr;
}
@media (min-width: 700px) {
  .training__grid {
    grid-template-columns: 1fr 1fr 1fr;
  }
}

.training__card {
  display: flex;
  flex-direction: column;
}

.training__card_text {
  margin-top: 0;
  margin-bottom: auto;
  padding-bottom: 20px;
}

.training__card_img {
  justify-self: center;
  align-self: center;
}

.training__card-header {
  margin: 20px 0 18px;
}

.training__text_block {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
}

.coach {
  background-color: #1D2124;
}

.coach__header {
  margin-bottom: 50px;
  text-align: center;
}

.coach__card {
  display: grid;
  grid-template-columns: 1fr;
  margin-top: 50px;
  grid-gap: 2em;
}
@media (min-width: 700px) {
  .coach__card {
    margin-top: 100px;
    grid-template-columns: 1fr 2fr;
    grid-gap: 1em;
  }
}

.coach__img {
  justify-self: center;
  align-items: center;
}
@media (min-width: 700px) {
  .coach__img {
    width: 250px;
    width: 250px;
  }
}

.coach__info {
  display: flex;
  flex-direction: column;
  justify-content: center;
  gap: 1em;
}

.coach__info p {
  padding: 0;
  margin: 0;
}

.coach__subheader {
  margin: 0;
  text-align: center;
}
@media (min-width: 700px) {
  .coach__subheader {
    text-align: left;
  }
}

.achievements {
  background-color: #1D2124;
}

.community {
  background-color: #0B0D0F;
}

.community__header {
  margin-bottom: 50px;
  text-align: center;
}

.community__text {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
}

.community__grid {
  display: grid;
  grid-template-areas: "a a" "c d" "f d" "f e" "g g";
  grid-gap: 20px;
}
@media (min-width: 700px) {
  .community__grid {
    grid-template-areas: "a a b" "a a b" "c d b" "c d e" "f d e" "f g g" "f g g";
  }
}

.img_1 {
  grid-area: a;
}

.img_2 {
  display: none;
}
@media (min-width: 700px) {
  .img_2 {
    display: block;
    grid-area: b;
  }
}

.img_3 {
  grid-area: c;
}

.img_4 {
  grid-area: d;
}

.img_5 {
  grid-area: e;
}

.img_6 {
  grid-area: f;
}

.img_7 {
  grid-area: g;
}

.how-to-join__header {
  text-align: center;
}

.how-to-join__text {
  text-align: center;
}

.how-to-join__text-block {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  padding-bottom: 10px;
}

.banner {
  display: none;
  background-color: #B43C3C;
  margin-right: auto;
  margin-left: auto;
  padding-top: 60px;
  padding-bottom: 60px;
}
@media (min-width: 700px) {
  .banner {
    display: block;
  }
}

.banner__text {
  font-size: 96px;
