  }
}

.info__subheader {
  margin-top: 0;
}

.contacts {
  background-color: #1D2124;
}

.contacts__header {
  margin-bottom: 50px;
  text-align: center;
}

.contacts__text {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
}

.contacts__grid {
  max-width: 960px;
  margin-right: auto;
  margin-left: auto;
  display: grid;
  grid-template-columns: 1fr 1fr;
